@media only screen and (max-width: 320px) {
  /* Row for top cards */
  .top-card-row {
    display: flex;
    gap: 16px; /* Smaller gap for mobile */
    flex-wrap: nowrap; /* Prevent wrapping to new lines */
    overflow-x: auto; /* Enable horizontal scrolling */
  }

  .top-card-row > .card {
    flex: 0 0 calc(100% - 16px); /* Full width minus gap for mobile */
    max-width: calc(100% - 16px); /* Prevent shrinking */
    margin-bottom: 16px; /* Adjust margin for mobile */
  }

  /* Bottom row for "Add another card" */
  .bottom-card-row {
    display: flex;
    gap: 16px; /* Smaller gap for mobile */
    flex-wrap: nowrap; /* Prevent wrapping to new lines */
    overflow-x: auto; /* Enable horizontal scrolling */
  }

  .bottom-card-row > .add-card {
    flex: 0 0 calc(100% - 16px); /* Full width minus gap for mobile */
    max-width: calc(100% - 16px); /* Prevent shrinking */
    margin-bottom: 16px; /* Adjust margin for mobile */
  }

  /* Scrollable card section */
  .card-columns {
    display: flex;
    gap: 16px; /* Smaller gap for mobile */
    padding-bottom: 10px;
  }
}
